import React from 'react'
import Link from 'gatsby-link'

const AboutSite = () => (
<div>
	<h1>About this site</h1>
	<p>I built this website using <a href="https://reactjs.org/">react.js</a> and <a href="https://www.gatsbyjs.org/">gatsby.js</a>.
	It was pretty easy. After taking the time to learn the basics of react I was able to get this small website off the ground in 
	just a couple of hours, so I'd definetly reccomend this technology to others getting started. Gatsby and React both play nicely
	with GraphQL so I will be using that to link in blog posts in the coming weeks. </p>
</div>
)

export default AboutSite


